.docs {
  overflow: hidden;
}
.docs h4 {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.docs .docs-item {
  float: left;
  width: 200px;
  height: 250px;
  padding: 0 10px;
  margin-right: 30px;
  margin-top: 30px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  background: #fffef5;
}
.docs .docs-item h4 {
  font-size: 14px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.docs .docs-item .docs-preview {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  padding-top: 5px;
  width: 180px;
  height: 185px;
  overflow: hidden;
}
.docs .docs-item .docs-preview h1,
.docs .docs-item .docs-preview h2,
.docs .docs-item .docs-preview h3,
.docs .docs-item .docs-preview h4,
.docs .docs-item .docs-preview h5,
.docs .docs-item .docs-preview h6,
.docs .docs-item .docs-preview blockquote,
.docs .docs-item .docs-preview blockquote p,
.docs .docs-item .docs-preview p,
.docs .docs-item .docs-preview ul,
.docs .docs-item .docs-preview li {
  font-size: 12px;
  margin: 0;
  padding: 5px 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
